<template>
  <div class="about">
    <p class="title">{{$t('french.about[0]')}}</p>
    <div class="flex">
      <div class="left">
        <p :class="idz==index?'left_p left_p_hov':'left_p'" v-for="(item,index) in tit" :key="index"
          @click="CLI(index)">{{item}}</p>
      </div>
      <div class="right">
        <p class="right_p">
          <i></i>
          {{tit[2]}}
        </p>
        <div v-html="aboutData" class="ddd"></div>
      </div>
    </div>
  </div>
</template>
<style lang='less'>
  @import "../../../assets/index.less";
  @import "about.less";
</style>
<script>
  import { agree } from "../../../api/c2c";
  export default {
    data() {
      return {
        tit: [this.$t('french.about[1]'), this.$t('french.about[2]'), this.$t('french.about[3]'), this.$t('french.about[4]'), this.$t('french.about[5]'), this.$t('french.about[6]')],
        Daz: ["reg_agree", "trans_agree", "jygz", "faq", "fxq", "xszd"],
        Daz_EN: ["reg_agree1", "trans_agree1", "jygz1", "faq1", "fxq1", "xszd1"],
        aboutData: null
      };
    },
    computed: {
      idz() {
        setTimeout(() => {
          this.getD();
        });
        if (this.$route.query.id) {
          return this.$route.query.id;
        } else {
          return 0;
        }

      }
    },
    methods: {
      getD() {
        var _this = this;
        if (this.$cookies.get('language') == 'English') {
          var sendData = {
            type: this.Daz_EN[this.idz]
          }
        } else {
          var sendData = {
            type: this.Daz[this.idz]
          }
        }
        agree(sendData).then(res => {
          switch (res.status == 200) {
            case true:
              _this.aboutData = res.data.data;
              break;
            default:
              _this.$public.msg(res.data.msg, "error", _this);
              break;
          }
        });
      },
      CLI(id) {
        //   this.$route.query.id = id;
        //   console.log(this.$route.query.id)
        this.$router.push({ name: "about", query: { id: id } });
      }
    }
  };
</script>


